.menu {
  padding: 0 20px;
  border-bottom: solid 1px #e8e8e8;
  overflow: auto;
  box-shadow: 0 0 30px #f3f1f1;
  background-color: rgba(0, 50, 0, 1);
}

.menu__logo {
  width: 150px;
  float: left;
  color: white;
}
.menu__container{
  background-color: rgba(0, 50, 0, 1);
  border-radius: 30px;
}
.menu__logo a {
  display: inline-block;
  font-size: 22px;
  font-weight: 600;
  padding: 19px 20px;
  color: white;
}
@media screen and (max-width:600px){

  .menu__logo a {
    display: inline-block;
    font-size: 18px;
    font-weight: 400;
    padding: 19px 20px;
    color: white;
  }

}

.menu__container .ant-menu-item {
  padding: 0px 5px;
  
}

.menu__container .ant-menu-submenu-title {
  padding: 10px 20px;
}
.howfar,
.menu__container .ant-menu-item a,
.menu__container .ant-menu-submenu-title a {
  padding: 10px 15px;
  font-size: 17px;
  font-weight: 600;
  color: rgba(0, 50, 0, 1) ;
}

.menu__container .ant-menu-horizontal {
  border-bottom: none;
  border-radius: 30px;
}

.menu__container .menu_left {
  margin-top: .9rem;
  float: left;
  background: rgba(0, 50, 0, 1) !important;
  border-radius: 30px;
}

.menu__container .menu_rigth {
  margin-top: .9rem;
  float: right;
  background: rgba(0, 50, 0, 1) !important;
  border-radius: 30px;
}

.menu__mobile-button {
  float: right;
  height: 32px;
  padding: 6px;
  margin-top: 8px;
  display: none !important; /* use of important to overwrite ant-btn */
  background: none;
  outline: none;
}
.bars{
  font-size: 1.5rem;
}

.menu_drawer .ant-drawer-body {
  padding: 0 !important;
}

/* align header of Drawer with header of page */
.menu_drawer .ant-drawer-header {
  padding: 14px 24px !important;
}

@media (max-width: 767px) {
  .menu__mobile-button {
    display: inline-block !important;
  }

  .menu_left,
  .menu_rigth {
    display: none;
  }

  .menu__logo a {
    margin-left: -20px;
  }

  .menu__container .ant-menu-item,
  .menu__container .ant-menu-submenu-title {
    padding: 1px 20px;
  }

  .menu__logo a {
    padding: 10px 20px;
  }
}
section h3{
    font-size: 25px;
    font-weight: 400;
    letter-spacing: 3px;
    text-shadow: 1px 1px 2px black;
    color: #fff;
}
section h1{
    margin: 30px 0 20px 0;
    font-size: 45px;
    font-weight: 700;
    text-shadow: 2px 1px 5px black;
    text-transform: uppercase;
    color: #fff !important;
}
.hip{
    z-index: 9;
    position: absolute;
    left: 3rem;
}
section p{
    font-size: 20px;
    font-weight: 300;
    color:rgb(26, 97, 50);
    word-spacing: 2px;
    margin-bottom: 25px;
    text-shadow: 1px 1px 1px black;
}
section a{
    padding: 12px 30px;
    border-radius: 4px;
    outline: none;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 13px;
    text-decoration: none;
    letter-spacing: 1px;
    transition: all .5 ease ;
}
section .btnone{
    background: #fff;
    color: #000;
    margin-right: 1rem
}
section .btntwo{
    background: rgba(0, 50, 0, 1);
    color: white;
    margin-left: 1rem;
    
}
section .btnone:hover{
    background: rgba(0, 50, 0, 1);
    color: white;
}
section .btntwo:hover{
    background: #fff;
    color: #000;
}

.change_content:after{
    content: '';
    -webkit-animation: changetext 10s infinite linear;
            animation: changetext 10s infinite linear;
    color: rgb(80, 181, 80);
}

@-webkit-keyframes changetext{
    0%{content: "lagos";}
    20%{content: "abuja";}
   35%{content: "imo";}
    60%{content: "calabar";}
    80%{content: "kano";}
   100%{content: "enugu";}
}

@keyframes changetext{
    0%{content: "lagos";}
    20%{content: "abuja";}
   35%{content: "imo";}
    60%{content: "calabar";}
    80%{content: "kano";}
   100%{content: "enugu";}
}
.homer{
    z-index: 0;
    position: relative;
    height: 9vh;
    top: -1rem;
}
